/* Des special width pour les animations contact*/
@media (min-width: 1024px) {
    .contact {
      
        &_message{
          font-size: 25px;
        }
      
        &_icons{
      
          .mouse{
            font-size: 45px;
            transform: rotate(-15deg);
            top: 80%;
            left: 65%;
          }
      
          .keyboard{
            font-size: 100px;
            top: 76%;
            left: 18%;
          }
          
          .desktop {
            font-size: 250px;
            top: 40%;
            right: 10%;
            z-index: 1;
          }
          
          .envelope {
            font-size: 25px;
            top: 55%;
            right: 50%;
          }
      
          .plant{
            width: 75px;
            right: 70%;
            top: 18%;
            z-index: 0;
          }
        }
    }
}
